    <fx-popover placement="bottom" trigger="click" titleText="显示标题" contentText="显示内容">
      <fx-button >Click me (bottom)</fx-button>
    </fx-popover>
    <fx-popover placement="right" trigger="focus" titleText="显示标题" contentText="显示内容">
      <fx-button >Focus me (right)</fx-button>
    </fx-popover>
  </div>
  <div class="wrapper">
    <v-md-preview :text="baseHtml" ></v-md-preview>
