  <div class="wrapper">
    <v-md-preview :text="selfHtml" ></v-md-preview>
  </div>
  <div class="wrapper">
    <v-md-preview :text="desc" ></v-md-preview>
  </div>
  <div class="bottom"></div>
</template>

<script>
import {ref} from "vue";

export default {
  name: "aafxPopover",
  setup(){
    const base = ref('# Popover 气泡卡片\n' +
        '点击/鼠标移入元素，弹出气泡式的卡片浮层。\n' +
        '## 何时使用\n' +
        '当目标元素有进一步的描述和相关操作时，可以收纳到卡片中，根据用户的操作行为进行展现。\n' +
        '## 基础用法\n' +
        '在 popover 中，需要包裹一个用于触发的元素，popover分为标题和内容部分，titleText 属性定义标题文本，contentText 属性定义内容文本\n' +
        'trigger属性用于定义popover的触发方式：hover，click 或者 focus。\n'+
        'placement属性用于定义popover出现的位置: top， left , bottom 或者 right。'
    )
    const baseHtml = ref('### 代码\n' +
        '``` html\n' +
        '<fx-popover placement="top" trigger="hover" titleText="显示标题" contentText="显示内容">\n' +
        '    <fx-button >Hover me (top)</fx-button>\n' +
        '</fx-popover>\n' +
        '<fx-popover placement="left" trigger="hover" titleText="显示标题" contentText="显示内容">\n' +
        '    <fx-button >Hover me (left)</fx-button>\n' +
        '</fx-popover>\n' +
        '<fx-popover placement="bottom" trigger="click" titleText="显示标题" contentText="显示内容">\n' +
        '    <fx-button >Click me (bottom)</fx-button>\n' +
        '</fx-popover>\n' +
        '<fx-popover placement="right" trigger="focus" titleText="显示标题" contentText="显示内容">\n' +
        '    <fx-button >Focus me (right)</fx-button>\n' +
        '</fx-popover>\n' +
        '```')

    const self = ref('## 自定义内容\n' +
        '可用title, content 的具名插槽slot，定义标题和内容，如果使用插槽，默认的内边距和背景颜色将会被取消。')

    const selfHtml = ref('### 代码\n' +
        '``` html\n' +
        '<fx-popover placement="right" trigger="click">\n' +
        '   <template #title>\n' +
        '       <div>自定义标题</div>\n' +
        '   </template>\n' +
        '   <template #content>\n' +
        '       <div>自定义内容</div>\n' +
        '   </template>\n' +
        '    <fx-button >Click me (right)</fx-button>\n' +
        '</fx-popover>\n' +
        '```')

    const desc = ref('## Popover属性\n' +
        '|属性|说明|类型|可选值|\n' +
        '|-|-|-|-|\n' +
        '|placement|出现位置|string|top/left/bottom/right|\n' +
        '|trigger|触发方式|string|hover/click/focus|\n' +
        '|titleText|标题文本|string|-|\n' +
        '|contentText|内容文本|string|-|')
    return{
      base,baseHtml,self,selfHtml,desc
    }
  }

}
</script>

